<template lang="pug">
    v-list-item
        v-list-item-content
            v-card
                v-card-title
                    span {{fileName}}
                v-container
                    v-row 
                        v-col(cols="12" xs="6" sm="6" lg="4")
                            span 已买金额:{{buymoney}}
                        v-col(cols="12" xs="6" sm="6" lg="4")
                            span 当前盈利：{{profit}}
                        v-col(cols="12" xs="6" sm="6" lg="4")
                            span 运行状态 
                            v-chip {{status}}
                        

                v-card-actions
                    v-row
                        v-col(cols="12" xs="6" sm="6" lg="3")
                            v-btn(color="success" dark @click="start") 启动
                        v-col(cols="12" xs="6" sm="6" lg="3")
                            v-btn(color="warnning" dark @click="stop") 暂停
                        v-col(cols="12" xs="6" sm="6" lg="3")
                            v-btn(color="warnning" dark @click="edit") 编辑
                        v-col(cols="12" xs="6" sm="6" lg="3")
                            v-btn(color="error" dark @click="remove") 删除

</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    name: "timstatuslistitem",
    props:{
        fileName:{
            type:String,
            default:"",
        },
        buymoney:{
            type:Number,
            default:0,
        },
        profit:{
            type:Number,
            default:0,
        },
        status:{
            type:String,
            default:"",
        },
    },

    methods:{
        go_updateconfig(){
            this.$router.push({path:"/tim/updateconfig"});
        },
        start(){
            api.TIMStartOne({FileName:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        stop(){
            api.TIMStopOne({FileName:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        remove(){
            api.TIMDeleteOne({filename:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        edit(){
            console.log(this.fileName)
            api.TIMGetOne({filename:this.fileName}).then(res=>{
                if (res.code==200){
                    console.log(res.data)
                    this.$store.dispatch("exbot/update_uptimconfig",res.data)
                    this.go_updateconfig()
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        }
    },



    
}
</script>


